.antimonopolnyykomplayens-content
{
    display: flex;
    row-gap: 30px;
    flex-direction: column;
    width: auto !important;
}

.antimonopolnyykomplayens-description{

}
.antimonopolnyykomplayens-description .value {

    font-weight: 500;
    font-size: 22px;
    line-height: 150%;
    color: #0078D2;
    @include mobile-768{
        font-weight: 400;
        font-size: 20px;
        line-height: 150%;
    }
}
.antimonopolnyykomplayens-subtitle span, .antimonopolnyykomplayens-description span span
{
    @include font-d-t1{
        line-height: 33px;
        color: black;
        @include mobile-768{
            font-weight: 400;
            font-size: 20px;
            line-height: 150%;
        }
    }
    margin-bottom: 30px;
}

.anothertitle{

    @include font-d-h5;
    @include mobile-768{
        font-weight: 500;
        font-size: 22px;
        line-height: 120%;
    }

}
.antimonopolnyykomplayens-target--list>ul{

    padding-left: 34px;
    padding-top: 20px;
}
.antimonopolnyykomplayens-target--list>ul>li{
    margin-bottom: 30px;
    &:last-child{
        margin: 0;
    }
    @include font-d-t1{
        list-style-type: disc;
    }
    @include mobile-768{
        font-weight: 400;
        font-size: 20px;
        line-height: 150%;
    }
}

.antimonopolnyykomplayens-target--list-pdf{
    display: flex;
    flex-direction: column;
    row-gap: 40px;
}
.antimonopolnyykomplayens-target--list--dowload{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    column-gap: 15px;
    margin-top: 0;
    a{
        font-weight: 400;
        font-size: 20px;
        line-height: 180%;
    }

}
