.advertisement-content-card{
    width: 310px;
    height: 205px;
    background-color: #EAF5FF;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include mobile-768{

    }

}

.advertisement-content-card__top{

    @include font-d-card-data;
    display: flex;
    flex-direction: row-reverse;

}

.advertisement-content-card__bottom{
    display: flex;
    flex-direction: column;
    gap: 8px;
    @include font-d-card-bottom;
    color: black
}
.advertisement-content-card__bottom-text{
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
}
.advertisement-content{
    display: flex;
    flex-direction: row;
    gap: 20px;
    @include notebook-1280{
        display: grid !important;
        grid-template-columns: repeat(2, 1fr) !important;
    }
    @include mobile-768{
        grid-template-columns:1fr !important;
        justify-content: center !important;
        place-items: center;
    }
}
.advertisement-content-card{
    @include notebook-1280{
        height:  205px;
        width: 100%
    }
    @include mobile-768{
        height: 194px;
        max-width:  375px;
    }
}
.advertisement-content-card__bottom-skidka{
    font-weight: 500;
    font-size: 30px;
    line-height: 100%;
    @include mobile-768{
        font-weight: 500;
        font-size: 28px;
        line-height: 150%;
    }
}
.advertisement-content-card__top{
    span{
        font-family: Roboto Condensed;
        font-size: 16px;
        font-style: italic;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: 0em;
        text-align: left;

    }
}
