.contacts-content{
    //width: 860px;
}


.contacts-content
{       
     @include mobile-768{
        overflow-x: scroll !important;

        
    }
    
    .another-table{
        @include mobile-768{
            width: 768px;
            table-layout: fixed;
        }
        tr{
            background-color: var(--color-light-blue);
        }
        tr:nth-child(2n){
            background: white;

        };
        tr{
            td:first-child{
                @include font-d-h6;
            }
            td:last-child{
                @include font-d-t1;
            }
            td{
                a{
                    color: var(--color-blue);
                }
            }
        }

        
    }
}


