
.footer-left-links--ul{
  display: flex;
  flex-direction: row;

  justify-content: space-between !important;
  @include notebook-1280{
    flex-direction: column;
    gap: 26px;
  }
  @include mobile-768{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 60px;
  }
  li{
    width: fit-content;

  }
}


.footer-left-icon{
  display: flex;
  justify-content: start;
  .main__logo{
    fill: white;
    stroke: #DDDDDD;
    background: transparent;
  }
}
.footer-left{
  flex: 2;
  display: flex;
  row-gap: 44px;
  flex-direction: column;
  @include notebook-1280{
    flex: 1;
  }
}
.footer-right{
  flex: 1;

}
.footer-content{

  padding: 40px 0 30px 0;
  @include mobile-768{
    padding-bottom: 40px;
  }
}
.footer-top{
  display: flex;
  justify-content: space-between;
}
.footer-right, .footer-right-con{
  display: flex;
  align-items: end;
  flex-direction: column;
  gap: 30px;
  .footer-right-con{
    gap: 10px;
  }
  @include mobile-768{
    align-items: center;
  }
}
.footer-bottom{
  margin-top: 81px;
  @include mobile-768{
    margin-top: 60px;
  }
  ul{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.mainlogowhite__logo{
}
.platformminilogo__icon{
  width: 24px !important;
  height: 24px !important;
}
.footer-bottom--logo{
  display: flex;
  gap: 7px;
}
.footer-section{
  //width: 100%;
  margin-left: 70px !important;
  margin-right: 70px !important;
  margin-bottom: 0 !important;
}
.social__link{
  width: 34px !important;
  height: 34px !important;
}
.footer-bottom, .footer-bottom--middle-table{
  ul{
    ul{
      display: flex;
      gap: 16px;
    }
  }
}
.footer-bottom--right, .footer-bottom--logo, .platformminilogo__icon{
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  display: grid;
  place-items: center;
  color: white;
}
.footer-left-links{
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  color: white;

}
.footer-right-con{
  color: white;
  span{
    font-family: 'Roboto Condensed';
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
  }
  a{
    font-weight: 400;
    font-size: 32px;
    line-height: 120%;
  }
}
.footer-bottom--middle-table{
  display: none;

  @include mobile-768{
    display: flex;
    gap: 16px;
    flex-direction: row;
  }
}
.footer-bottom--middle{
  @include mobile-768{
    display: none !important;
  }

}
footer{
  background: var(--color-blue-gradient);
}

.footer-top{
  @include mobile-768{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.footer-bottom{
  ul{
    @include mobile-768{
      flex-direction: column;
    }
  }
}

.footer-bottom--logo{
  @include mobile-768{
    flex-direction: row !important;
    justify-content: center !important;
    gap: 7px;
  }
}
.footer-bottom--right{
  @include mobile-768{
      margin-bottom: 40px;
  }
}
.mainlogowhite__logo{
  width: 237.83px;
  height: 43px;
}
.footer-section{
  @include mobile-768{
    margin: 0 !important;
  }
}
