.market-form{
    display: flex;
    row-gap: 50px;
    padding-bottom: 80px;
    flex-direction: column;
    @include mobile-768{
        gap: 0;
        padding-bottom: 0px;
    }

    form{
        border: none;
        outline: none;
    }
    input{
        border: none;
        outline: none;
        border-bottom: 2px solid #0078D2;
        font-weight: 400;
        font-size: 22px;
        line-height: 16px;
        color: #000000;
    }
}

.market-form-data, .market-form-startprice, .market-form-search{
    display: flex;
    flex-direction: column;

}

.market-form-data--inputs, .market-form-startprice--inputs{
    display: flex;
    column-gap: 20px;
    height: 75px;
    input{
        padding-bottom: 15px;
        display: flex;
        align-items: end;
        width: 310px;
        text-align: start;

    }

}
.market-form-data{
    flex-direction: column;

    .market-form-data--text{
        font-weight: 500;
        font-size: 24px;
        line-height: 150%;
        color: #000000;

    }
    .market-form-data--anothertext{

        color: #0078D2;
        font-weight: 400;
        font-size: 18px;
        line-height: 12px;
    }
}

.market-form-search{

    .market-form-search--input{
        input{
            padding-bottom: 15px;
        }
    }

}
.market-form-startprice--text, .market-form-search--text {
    font-weight: 500;
    font-size: 24px;
    line-height: 150%;
    color: #000000;
}
.market-form-startprice--inputs{
    display: flex;
    align-items: end;

}
.market-form-search--input{
    display: flex;
    gap: 20px;
    height: 75px;
    align-items: end;
    input{
        width: 530px;
    }
    @include mobile-768{
        height: auto;
        gap: 0;
    }

}
.market-form-search--btn{
    width: 90px;
    height: 50px;
    display: grid;
    place-items: center;
    background: #0078D2;
    border-radius: 5px;
    font-weight: 500;
    font-size: 20px;
    line-height: 100%;
    color: white;
    @include mobile-768{
        width: 120px;
        height: 50px;
        
    }
}
.market-table{
    @include mobile-768{
        margin-bottom: 0;
    }
    tr{
        display: grid;
        //grid-template-columns: 120px 370px 110px 120px !important;
        grid-template-columns: 120px 1fr 110px 120px !important;
        @include mobile-768{
            grid-template-columns: 120px 370px 110px 120px !important;

        }
    }
    tr:not(:first-child){
        td:nth-child(2){
            text-decoration: underline;
            color: #0078D2;
        }
    }
    tr:first-child{
        align-items: center;
    }

}

.market-form-search--text, .market-form-startprice--text, .market-form-data--text{
    @include mobile-768{
        font-weight: 500;
        font-size: 22px !important;
        line-height: 150%;
    }
}
.zakupochnyyeprotsedury-body{

        @include mobile-768{
            overflow-x: scroll;
        }
        .zakupochnyyeprotsedury-table.main-table.another-table.market-table{
        @include mobile-768{
            table-layout: fixed;
        }

    }
}

.market--botoom--table{
    margin-bottom: 100px;
    @include mobile-768{
        margin-bottom: 40px;
    }
}
input{
    position: relative;
    overflow: visible;
    padding-bottom: 0;
}
input[type="date" i]::-webkit-calendar-picker-indicator{
    background-image: url('../img/calendar.svg');
    width: 32px;
    height: 32px;
    z-index: 1000;
    @include mobile-768{
        position: absolute;
        right: 0;
        bottom: -47px;

    }
}
.market-form-search, .market-form-startprice, .market-form-data {
    flex-shrink: 0;
}

.market-form-startprice--inputs, .market-form-data--inputs, .market-form-search--input{
    @include mobile-768{

        flex-direction: column;
        align-items: flex-start;
        margin: 0;
        padding: 0;
        grid-template: 0;
        margin-bottom: 100px !important;
        input{
            height: 75px !important;
            padding-top: 50px;
            margin-bottom: 20px;
            width: 100%;
        }
    }   

}
.market-form-data, .market-form-startprice{
    @include mobile-768{
        margin-bottom: 50px;
    }
}
.market-form-search{
   @include mobile-768{
    //margin-bottom: 80px;
   }
}
