.zakupochnyyeprotsedury-table
{
    margin-bottom: 113px;
    display: flex;
    row-gap: 30px;
    tr{
        border-radius: 5px;
        display: grid;
        //grid-template-columns: 40px 210px 320px 125px;
        grid-template-columns: 40px 1.5fr 2.5fr 1fr;
        column-gap: 40px;
        td
        td{
            @include font-d-t2;
        }
        td:nth-child(4){
            span{
                color: var(--color-blue);
                display: flex;
                align-items: center;
                column-gap: 2px;
                transform: .3s;
            }
        }
    }
    tr:first-child{
        td{
            @include font-d-h6;
                color: black;
        }
    }
    tr:nth-child(2n+1){
        .zakupochnyyeprotsedury-table--line{
            background-color: white;
    
        }
    }


}
.chevron-up__icons{
width: 24px;
height: 24px;
display: grid;
place-items: center;
transform: .3s;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 4 столбца */
    grid-template-rows: auto auto; /* 2 строки */

}

.block1 {
grid-column: 1;
grid-row: 1; 

}

.block2 {
grid-column: 2; 
grid-row: 1; 

}

.block3 {
grid-column: 3; 
grid-row: 1; 

}

.block4 {
grid-column: 4; 
grid-row: 1;

}

.block5 
{
    display: none;
    grid-column: 1 / span 4;
    grid-row: 2; 
}
.zakupochnyyeprotsedury-table--line 
{
    margin: 30px 0;

    width: 100%;
    height: 2px;
    background-color: var(--color-light-blue);
}


.zakupochnyyeprotsedury-table--line-content{

    .uchreditelnyyedokumenty-content.antimonopolnyykomplayens-content{
        margin-top: 30;
        .antimonopolnyykomplayens-target--list--dowload{
            column-gap: 20px;
            margin-top: 0;
        }
    }
}
.chevron-up__icons{
    width: 24px;
    height: 24px;
    transition: .2s;
}
.block5on{
    display: block; 
} 
.chevron-up__icons__add{
    transform: rotate(180deg);
    transition: .2s;
}
.zakupochnyyeprotsedury-body {
    @include mobile-768{
        overflow-x: scroll;
        margin-bottom: 44px;

    }
    table{
        @include mobile-768{
            table-layout: fixed;
            margin-bottom: 44px;
        }
    }
}