.page1-container{
  padding: 0 203px;
  margin-top: 60px;
  @include notebook-1280{
    padding: 0 103px;
  }
  @include mobile-768{
    padding: 0 20px;
  }

}
.page1--image{
  position: relative;
  overflow: hidden;
  max-width: 894px;
  height: 620px;


  @include notebook-1280{
    position: relative;
    width: auto;
    height: 505px;


  }
  @include mobile-768{
    width: auto;
    height: 232px;
  }

  img{
    width: 100%;
    height: 100%;
    object-fit: cover;	


  }


  background: #C4C4C4;
  border-radius: 5px;
  margin-bottom: 49px;
}
.page1--title{
  font-weight: 400;
  font-size: 40px;
  line-height: 150%;
  @include mobile-768{
    font-size: 28px;
    line-height: 120%;
  }

}
.page1--content{

  ul{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    li{
      font-weight: 400;
      font-size: 22px;
      line-height: 150%;
      @include mobile-768{

        font-size: 20px;
        font-weight: 400;
        line-height: 30px;

      }
    }
  }
}
.page1--slider{
  width: 100%;
  height: 499px;
  @include notebook-1280{
    height: 394px;

  }
  @include mobile-768{
    height: 212px;
  }
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  margin-top: 50px;
  @include notebook-1280{
    width: auto;

  }
  @include mobile-768{

  }

}
.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.page1--slide-button-next, .page1--slide-button-prev{
  position: absolute;

}
.page1--swiper{
  width: 100%;
  height: 442px;
  background-color: #3bbebe;
  border-radius: 5px;
  @include notebook-1280{
    height: 337px;
  }
  @include mobile-768{
    height: 155px;
  }
}
.page1--slide{
  //display: grid;
  //place-items: center;

  img{
    width: 100% !important;
    height: 100% !important;
    object-fit:cover;
  }

}
.line1__icon, .line2__icon{

  width: 50px;
  display: flex;
  z-index: 999;
  @include mobile-768{
    width: 40px;
  }

}
.page1--pagination{
  position: absolute;
  right: 0;
  bottom: 0;
  @include mobile-768{
    bottom: 10px !important;
  }
  display: flex !important;
  justify-content: flex-end !important;
  align-items: flex-end !important;

}
.swiper-pagination-total{
  font-family: 'Roboto';
  font-style: italic !important;
  font-weight: 400;
  font-size: 22px;
  @include mobile-768{
    font-size: 20px !important;
  }
  line-height: 100%;
  text-align: start;
}
.swiper-pagination-current{
  font-family: 'Roboto';
  font-style: italic;
  font-weight: 400;
  font-size: 40px;
  line-height: 100%;
  color: #0078D2;
  text-align: center;
  @include mobile-768{
    font-size: 30px !important;
  }
}
.page1--swiper--btn
{
  position: absolute;
  display: flex;
  gap: 30px;
  bottom: 0;

}
.page1active{
  color:#0078D2;
}

.page2--image{
  overflow: hidden;
  margin-top: 40px;
  max-width: 894px;
  height: 441px;
  background: #C4C4C4;
  border-radius: 5px;
  margin-bottom: 20px;
  
  @include notebook-1280{
    width:auto;
    height: 336px;

  }
  @include mobile-768{
    height: 155px;

  }

}
.page2--image--subtext{
  font-family: 'Roboto Condensed';
  font-style: italic;
  font-weight: 400;
  font-size: 22px;
  line-height: 150%;
  color: #C6C6C6;
  @include mobile-768{
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
  }

}
.page2--author{
  margin-top: 40px;
  font-weight: 400;
  font-size: 22px;
  line-height: 150%;
  @include mobile-768{
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
  }
}


//page 3
.pustou__block{
  height: 30px;
}
.page3--title{
  display: flex;
  flex-direction: column;
  row-gap: -10px;
}
.page1--wrapper{
  height: 100%;
  width: 100%;
}
.page1--slide{
  width: 100%;
  height: 100%;
  img{
    width: 100%;
    height: 100%;

  }
}