
header{
    position: fixed;
    top: 0 !important;
    z-index: 2000;
    width: 100%;
    //z-index: 11;
    z-index: 1200;
    background: white;

    .header-content{

        display: flex;
    }
    @include notebook-1280{
        margin-bottom: 40px;
    }
}
nav{

    flex: 1;
    height: 80px !important;
    display: flex;
    align-items: center;

    margin: 0 70px;
    border-bottom: 1px solid #EAF5FF;
    @include notebook-1280{
        margin: 0 20px;
    }
    justify-content: space-between;

    .middle__links{
        flex-basis: 668px;
        display: flex;
        justify-content: space-between;
        li{
            a{
                font-weight: 400;
                font-size: 17px;
                line-height: 100%;

                &:hover{
                    transition: 0.2s;
                    color: #0078D2;
                    padding-bottom: 30px;
                    border-bottom: 3px solid #0078D2;
                }
            }
            &:hover a{
                transition: 0.2s;
                color: #0078D2;
                padding-bottom: 28px;
                border-bottom: 3px solid #0078D2;
            }

        }
        @include notebook-1280{
            display:none;
        }
    }
    .right__link{
        display: flex;
        align-items: center;
        flex-basis: 251px;
        justify-content: space-between;
        .logo__header{
            .main__logo{
                width: 251px;
                height: 45px;
                @include mobile-768{
                    
                }
            }
        }
    }
    .left__links{
        flex-basis: 231px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include notebook-1280{
            display:none;
        }
    }
    .header__cards{
        display: flex;
        align-items: center;
        column-gap: 5px;
        div{
            font-weight: 400;
            font-size: 17px;
            line-height: 100%;
        }
    }
}
.header__search{
    width: 28px;
    height: 28px;
}
.header__card{
    width: 24px;
    height: 24px;
}

.header__burger{
    width: 42px;
    height: 42px;
    display: none;
    @include notebook-1280{
        display: grid;
        place-items: center;
    }

}
.burgermenu__link{
    width: 42px;
    height: 42px;
    display: grid;
    place-items: center;
    animation-duration: .3s;
    animation-name: slidein;
}
.mobile--header{
    position: relative;
    display: none;
    
    @include notebook-1280{
        position: fixed;
        top: 80px;
        z-index: 10000;
        width: 100%;
        height: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        row-gap: 56px;
        background-color: #EAF5FF;
        li{
    
            font-size: 22px;
            font-weight: 400;
            line-height: 22px;
            letter-spacing: 0px;
        }
    }
}

.top--header{
    top: 0px !important;
}
.btnclose__link{
    display: none;
    place-items: center;
    width: 42px;
    height: 42px;

    @include mobile-768{
        width: 32px;
        height: 32px;
    }
}

.burgermenu__link__btn{
    z-index: 10000;
    top: 20px;
    right: 20px;
    position: absolute;
    display: none;
    place-items: center;
    width: 42px;
    height: 42px;
    @include mobile-768{
        width: 32px;
        height: 32px;
    }
}

@keyframes slidein {
    0% {
        opacity: 0;
        transform: scale(0, .025),

      }
      50% {

        transform: scale(1, .025)
      }
      100% {
        opacity: 1;
      }
  }
@keyframes slidein1 {
    0% {
        border-radius: 50%;
        transform: scale(2, 0),

      }
      50% {
        border-radius: 50%;
        transform: scale(0, .025)
      }

  }

.topnull{
    top: 0 !important; 
}
.displayFlex{
    display: flex !important;
    animation-duration: .4s;
    animation-name: slidein;
}
.displayFlex1{
    display: flex !important;
    animation-duration: .2s;
    animation-name: slidein1;
}
.displayGrid{
    display: grid !important;
    animation-duration: .6s;
    animation-name: slidein;

}
.displayNone{
    display: none !important;
}

.activeHeader-item{
    color: #0078D2;
    padding-bottom: 28px;
    border-bottom: 3px solid #0078D2;
}
.activeHeader-item--left{
    color: #0078D2;
}
.header__search{
    background: transparent;
    display: grid;
    place-items: center;
}

.modalSearchHeader__wrap{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 500px;
}
.custommodalheadersearch{
    display: none;
    z-index: 1000;
    position: fixed;
    top: 80px;
    width: 100vw;
    height: 100vh;
    background-color: black;
    padding: 129px 70px;
    color: white;
    span, input{

        font-size: 40px;
        font-weight: 400;
        line-height: 60px;
        letter-spacing: 0px;


    }
}
.custommodalheadersearch#appSearch{
    justify-content: center;
}
.custommodalheadersearch--body{
    height: fit-content;
    display: flex;
    flex-direction: row;
    input{
        height: 63px;
        color: white;
        width: 1219px;
        border: none;
        outline: none;
        border-bottom: 3px solid #0078D2;
        padding: 0;
        padding-left: 20px;
        background: transparent;
        &::placeholder{
            color: white !important;
        }
    }
    button{
        background: transparent;
        display: grid;
        place-items: center;
    }
}
.searchBig__icon{
    width: 42px;
    height: 42px;

}