.auditoriregistrator-content{
    //max-width: 860px;
    .auditoriregistrator-content--table--content{
        margin-bottom: 100px;
        @include mobile-768{
            overflow-x: scroll;
        }
        table{
            width: 100%;
            margin-bottom: 0px;
            @include mobile-768{
                width: 768px;
                table-layout: hidden;
            }
            tr{
                td:first-child{
                    @include font-d-h6;
          
                }
                td:last-child{
                    @include font-d-t2;
    
                }
            }
        }
    }


}
.korporativnoyeupravleniye-title.auditoriregistrator-title {
    span{
        @include mobile-768{
            font-weight: 400;
            font-size: 32px;
            line-height: 120%;
        }
    }
}