.news-swiper-title{
    @include mobile-768{
        padding-bottom: 56px !important;
    }
}
.news-swiper-bottom-link{
    @include mobile-768{
        position: absolute;
        bottom: 30px !important;
        right: 0 !important;
        // align-self: flex-end !important;
    }
}
.news-swiper-bottom{
    @include mobile-768{
        padding-bottom: 28px !important;
    }
}


.news-content{
    width: 100%;
    min-height: 454px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    @include notebook-1280{
        width: auto !important;
        height: auto !important;
        display: flex;
        flex-direction: column;
        row-gap: 20px;
    }



}
.news-title{
    @include font-d-h2
}
.news-content-right, .news-content-left{
    border-radius: 5px;
    position: relative;
    overflow: hidden;
}
.news-content-left{
    width: 100%;
    padding-bottom: 27px;
    z-index: 1000;

    @include notebook-1280{
        padding-bottom: 0;
    }
    @include mobile-768{
        min-height: 351px !important;
    }

}

.news-swiper-bottom{
    @include mobile-768{
        display: flex !important;
        flex-direction: column !important;
    }
}

.news-content-right{

}
.news-swiper-img{
    width: 640px;
    height: 325px;
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    img{
        transition: 1s;
        display: block;

    }
    img:hover{
        transform: scale(1.1);

    }
    @include notebook-1280{
        width: 100% !important;
        height: 325px !important;
    }
    @include mobile-768{
        height: 170px !important;
    }

}
.news-swiper-bottom{
    padding: 0 21px 0 8px;

}
.news-swiper-content{
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;

}
.news-swiper-data{
    padding-left: 8px;
    font-family: 'Roboto Condensed';
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #0078D2;
    margin-top: 23px;
}
.news-swiper-title{

    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    margin-top: 12px;
    width: 70%;
    @include notebook-1280{
        padding-bottom: 30px;
    }

}
.news-swiper-bottom-link{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    gap: 10px;
}

.news-swiper-link{
    font-style: italic;
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
    display: grid;
    place-items: center;
}
.news-swiper-bottom{
    flex-basis:content;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}
.smallvectortoright__icon{
    width: 8px !important;
    height: 16px !important;
}
.news-swiper-scrollbar{

    position: absolute !important;
    z-index: 999 !important;
    bottom: 0 !important;
    width: 640px !important;
    background: #EAF5FF !important;
    border-radius: 5px !important;
    overflow: hidden;
    @include notebook-1280{
        width: 100% !important;
    }


}
.news-drag{
    height: 2px;
    background: #0078D2;
    border-radius: 5px;
}

//right
.news-content-right{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;

    @include mobile-768{
        display: flex !important;
        flex-direction: column !important;
        row-gap: 20px;
    }
}
.news-content-right-cards{
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #EAF5FF;
    position: relative;
    border-radius: 5px;
    @include notebook-1280{
        width: 100%;
        height: 454px;
    }
    @include mobile-768{
        height: 430px !important;
    }
}
.news-content-right-cards--img{

    width: 100%;
    height: 179px;
    overflow: hidden;
    img{
        transition: 1s;
        display: block;

    }
    img:hover{
        transform: scale(1.1);

    }
    @include notebook-1280{
        width: 100%;
    }
    @include mobile-768{
        height: 220px;
    }
    @media (max-width: 425px){
        height: 170px !important;
    }

}
.news-content-right-cards--data{
    padding-left: 22px;
    font-family: 'Roboto Condensed';
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #0078D2;
    margin-top: 23px;
    @include notebook-1280{
        margin-top: 30px;
    }
}
.news-content-right-cards--desc{

    margin-left: 22px;
    padding-right: 22px;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    margin-top: 12px;
    @include notebook-1280{
        margin-top: 18px;
        padding-right: 25.12px;
    }

}
.news-content-right-cards--bottom{
    position: absolute;
    left: 22px;
    bottom: 31px;
    z-index: 100;
}

.news-swiper-bottom-link{
    @include notebook-1280{
        align-items: center;

    }
}
.news-swiper-link{
    @include mobile-768{
        font-family: Roboto;
        font-size: 16px;
        font-style: italic;
        font-weight: 500;
        line-height: 16px;
        letter-spacing: 0px;
        text-align: left;

    }
}
