.azc-content{
    width: 100%;

    position: relative;
}
.azc-map{
    width: 100%;
}
#app1{
    border-radius: 5px;
    overflow: hidden;
    width: 100% !important;
    background: #EAF5FF;
    display: flex;
    flex-direction: row !important;
    height: 100%;
    @include mobile-768{
        flex-direction: column !important;
    }
    #map{
        width: 100%;
        flex: 1;
        height: 560px !important;
        @include mobile-768{
            flex-basis: 400px;
            .mapazc--map--input{
                input{
                    font-size: 18px;
                    font-weight: 400;
                    line-height: 18px;
                }
            }
        }
    }
    .mapazs-content-right--option{
        flex-basis: 310px;
        overflow-y: scroll;
        height: 560px ;
        @include mobile-768{
            // flex-basis: 548px;
            flex: 1;
        }

    }

}
