.mainslider{
    // width: 1300px;
    width: 100%;
    height: 700px;
    border-radius: 5px;
    margin-top: 40px;
    position: relative;
    overflow: hidden;
    @include notebook-1280{
        height: 650px;
    }
}
.mySwiper{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
}
.slide-content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}
.slide-content{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    column-gap: 18px;
    padding-left: 40px;
    @include mobile-768{
        padding-left: 22px;  
    }
    .slide-title{
        width: 70%;
        @include font-d-h1;
            color: white;
            line-height: 100%;

        @include mobile-768{
            font-weight: 400 !important;
            font-size: 40px !important;
            line-height: 120% !important;
        }
    }

    .slide-subtitle{
        font-weight: 400;
        font-size: 24px;
        line-height: 120%;
        color: white;
        margin-bottom: 153.46px;

        @include mobile-768{
            font-weight: 400;
            font-size: 18px;
            line-height: 120%;
        }
}
}
.slide-img{
    //width: 100%;
    //height: 100%;
    //position: absolute;
}
.slide-content{
    position: relative;
    background: var(--color-gradient);
}


.swiper-scrollbar{
    position: absolute !important;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 7px;
    display: flex;
    align-items: center;
    // width: 1210px !important;
    height: 1.79px;
    margin: 0;
    top: 635.38px !important;
    margin-left: 40px;
    left: 0;
    padding: 0;
    ///
    width: 100% !important;
    @include notebook-1280{
        top: 608px !important;
        margin-left: 22.4px;
    } 

}
.swiper-scrollbar-drag{
    width: 186.15px !important;
    height: 1.79px;
    background: #FFFFFF;
    border-radius: 7px;
    z-index: 1000;

    @include notebook-1280{
        width: 104.25px !important;
    }
}

.swiper-pagination{
    width: 100% !important;
    margin-left: 40px;
    position: absolute !important;
    // max-width: 100px !important;
    display: flex;
    flex: 1;
    // gap: 285.15px;
    // margin-left: 35px;
    top: 606.87px !important;
    justify-content: space-between;
    // margin-right: 100px !important;

    ///
    // width: 100px !important;
    @include notebook-1280{
        top: 574px !important;
        margin-left: 22.4px;
    }
}
.swiper-pagination-bullet {
    // width: 100vw !important;
    // margin-left: 100px !important;
    flex: 1 !important;
    text-align: left !important;
    text-align: center;
    font-family: 'Roboto';
    font-style: italic;
    font-weight: 400;
    font-size: 22px;
    line-height: 100%;
    color: white;
    opacity: 1;
    background: transparent;
    transition: 1s;
}

