    // width: 860px;


}
.contactsazs-content{
    @include mobile-768{
        overflow-x: scroll !important;
    }

    table{

        @include mobile-768{
            table-layout: fixed !important;
            width: 860px;
        }
        tr{
            @include font-d-t2;
            display: grid;
            column-gap: 40px;
            grid-template-columns: 1fr 1fr 181px;
        }
        tr:first-child{
            @include font-d-h6;
        }
    }
}
