.vakansii--modat--custom-celect{
    display: flex;
    justify-content: space-between;
    position: relative;
    width: 100%;
    height: 75px;
    border-bottom: 2px solid #0078D2;
    display: flex;
    align-items: flex-end;

    font-size: 22px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0em;
    span{
        padding-bottom: 18px;
    }
    div{
        display: flex;
        align-items: flex-end;
    }
    ul{
        background: #0078D2;
        z-index: 1000;
        box-sizing: border-box;
        position: absolute !important;
        display: none;
        flex-direction: column;
        color: #FFFFFF !important;
        top: calc(75px) ;
        width: 100%;
        padding: 20px 20px;

        border-radius: 0 0 5px 5px;
        border: 1px solid  #0078D2;
        border-top: none;
        li{
            padding-bottom: 20px;
            &:last-child{
                padding-bottom: 0 !important;
            }
        }
        li:hover{

            transition: 0.5s;

            &::after{
                content: "<-";
                width: 20px;
                height: 20px;
                margin-left: 20px;

            }
        }
    }
}

