.vakansii--modat--custom--footer{

    display: grid;
    grid-template-columns: 48px 1fr;
    column-gap: 18px;
    align-items: center;
}
.vakansii--modat--custom--footer-toggle{
    display: grid;
    place-items: center;
    width: 48px;
    height: 48px;
    input[type=checkbox]{
        height: 0;
        width: 0;
        visibility: hidden;
      }
      
      label {
        cursor: pointer;
        text-indent: -9999px;
        width: 46px;
        height: 24px;
        background: rgb(0, 0, 0);
        display: block;
        border-radius: 100px;
        position: relative;
      }
      
      label:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 5px;
        width: 18px;
        height: 18px;
        background: #fff;
        border-radius: 90px;
        transition: 0.3s;
      }
      
      input:checked + label {
        background: #0078D2;
      }
      
      input:checked + label:after {
        left: calc(100% - 5px);
        transform: translateX(-100%);
      }
      
      label:active:after {
        width: 10px;
      }

}
.vakansii--modat--custom--footer-message{
