.otvetstvennost-content{
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    // @include table{
    //     max-width: 720px;
    // }
}
.otvetstvennost-card-content{
    word-wrap: break-word;
    //width: 750px;
    .value {

        font-family: Roboto;
        font-size: 22px;
        font-weight: 500;
        line-height: 33px;
        letter-spacing: 0px;
        text-align: left;
        color: #0078D2;
        @include mobile-768{
            font-size: 20px;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: 0px;


        }
    }
    span{
        span{
            font-family: Roboto;
            font-size: 22px;
            font-weight: 400;
            line-height: 33px;
            letter-spacing: 0px;
            text-align: left;
            color: black;
            @include mobile-768{
                font-size: 20px;
                font-weight: 400;
                line-height: 30px;
                letter-spacing: 0px;
    
    
            }
        }
  
    }
}
.otvetstvennost-card-content{
    display: flex;
    flex-direction: column;
    row-gap: 33px;
    margin-top: 31px;
}
.otvetstvennost-card-title-bottomtitle
{
    @include font-d-h5;
    margin-top: 19px;
    @include mobile-768{
        font-weight: 500;
        font-size: 22px;
        line-height: 120%;
    }

}
.otvetstvennost-card-title-bottomtitle-list  {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    padding-left: 14px;

}
.otvetstvennost-card-container 
{

    display: inline-flex;
    column-gap: 10px;
    @include notebook-1280{
        display: flex;
        justify-content: space-between;
    }
}
.otvetstvennost-card-text{
    display: inline-block;
    width: fit-content;


    @include font-d-h4;
    div{
        display: inline-block;

    }
    @include mobile-768{
        font-weight: 400;
        font-size: 24px;
        line-height: 120%;
    }
}
.otvetstvennost-card-icon{
    display: grid;
    place-items: center;
}
.chevron-up-icon{
    width: 40px;
    height: 40px;
    transform: rotate(180deg);
    transition: 0.3s;
}
.otvetstvennost-card-content{
    display: none;
}
.displayblock{
    display: flex;
}
.rotatedefault{
    transform: rotate(0);
    transition: 0.3s;
}
.otvetstvennost-card-icon{
    .chevron-up-icon{
        width: 40px;
        height: 40px;
    }
}
.right-content-main{
    margin: 0 auto;
}