.banner-content{
    border-radius: 5px;
    box-sizing: border-box;
    max-width: 1300px;
    height: 290px;
    background: var(--color-linear-gradient);
    padding: 45px 110px 0 110px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    overflow: hidden;
    @include notebook-1280{
        height: 533px;
    }
    @include mobile-768{

        height: 415px !important;
    }
}                    
.banner-content-left-icons{
    display: flex;
    flex-direction: row;

}
.banner-content-left-title{
    @include font-d-h4{
        color: white;
    }
    margin-bottom: 15px;
}
.banner-content-left-text{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: white;

}
.banner-content-left-icons{
    margin-top: 24px;
}
.banner-content-right {
    position: relative;
}

.apps__icon{

    width: 262px;
    height: 38px;
}
.phones__icon{
    width: 439px;
    height: 246px;
}

//новое обновление
.banner-content-right{
    display: flex;
    align-items: flex-end;
    justify-content: center;

}

.banner-content-left-icons{
    @include notebook-1280{
        margin-top: 30px;
    }
}
.banner-content-left{
    @include notebook-1280{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        h3, span{
            text-align: center;
        }
    }
    @include mobile-768{
        h3{
            font-weight: 500;
            font-size: 22px;
            line-height: 120%;
        }
        span{
            font-weight: 400;
            font-size: 16px;
            line-height: 120%;
        }
    }
}
.banner-content-right {
    @include mobile-768{

    }
    position: relative;
    @include notebook-1280{
        width: 100% !important;
        height: 100% !important;

        .phones{
            width: 439px !important;
            height: 246px !important;
            position: static;
            display: flex;
            justify-content: space-between !important;
            align-items: center !important;
            .phones__icon{
                position: static;
                @include mobile-768{
                    display: flex;
                    align-items: flex-end;
                    justify-content: flex-end;
                    height: auto !important;
                }

            }
        }
    }

}
.banner-content{
    justify-content: space-between;
    @include notebook-1280{
        display: flex !important;
        flex-direction: column !important;
        height: 533px;
        padding: 0;
        padding-top: 45px;
    }

}
.phones__icon{
    object-fit: contain;
    @include mobile-768{
        width: 100%;
        height: 100%;
        object-fit: cover !important;
    }

}
.phones__icon{
    width: 100%;
    height: 100%;
    object-fit: fill;
    @include mobile-768{
        width: auto;
        height: auto;
        object-fit:fill;
    }
}
.phones{
    @include mobile-768{
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        padding-left: 33px;
        padding-right: 33px;

    }
}
