.kontaktydlyasmi-content{
    @include mobile-768{
        overflow-x: scroll;
    }
}
.kontaktydlyasmi-content--table{
    @include mobile-768{
        width: 768px;
        table-layout: fixed;
    }
    width: 100%;
    tr:not(:first-child){
        td:not(:first-child){
            color: var( --color-blue);
            text-decoration:underline;
        }
    }
    tr{
        display: grid;
        grid-template-columns: 1fr 1fr;
        height: 90px;
        align-items: center;
        background-color: var(--color-light-blue);
        padding-left: 20px;
    };
    tr:nth-child(1){
        height: 120px;
    };
    tr:nth-child(2){
        background-color: white;
    };
    
    

}
.kontaktydlyasmi-content--table--name, .kontaktydlyasmi-content--table--email, .kontaktydlyasmi-content--table--phone{
    @include font-d-h6;
}
.kontaktydlyasmi-content--table--namevalue, .kontaktydlyasmi-content--table--emailvalue, .kontaktydlyasmi-content--table--phonevalue{
    @include font-d-t2{
        line-height: 30px;
    }
}