.zadatvopros-content
{
    // max-width: 640px;
    width: 100%;

}
.zadatvopros--form{
    display: flex;
    flex-direction: column;
    row-gap: 30px;
    justify-content: space-between;
    .zadatvopros--form-input{
        display: flex;
        align-items: end;
        input{
            width: 640px;
            border: none;
            outline: none;
            border-bottom: 2px solid #0078D2;
            padding-bottom: 10px;
            font-size: 22px;
            font-weight: 400;
            line-height: 16px;
            @include notebook-1280{
                width: 100%;
            }
        }


    }
    textarea{
        height: 200px;
        border: none;
        outline: none;
        background: #EAF5FF;
        padding: 15px 19px;
        font-weight: 400;
        font-size: 22px;
        line-height: 150%;
        @include mobile-768{
            // width: 335px;
            height: 200px;
            margin-top: -5px;
            margin-bottom: 2px;

        }

    }
    .zadatvopros--form-input:nth-child(2){
        margin-bottom: 30px;
    }

}
.zadatvopros--form-label {
    font-weight: 500;
    font-size: 22px;
    line-height: 100%;
}
.zadatvopros--form-input{
    height: 75px;
    @include mobile-768{
        // width: 335px !important;

    }
}

.tumbler
{
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    input{
        display: none;
    }
}
.slider{
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000000;
    -webkit-transition: .4s;
    transition: .4s;
}
.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
input:checked + .slider {
    background-color: #000000;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}
.zadatvopros--form-toggle{
    display: flex;
    align-items: center;
    column-gap: 18px;
    span{
            font-size: 20px;
            font-weight: 400;
            line-height: 30px;
            a{
                color: #0078D2;
            }
    }
}