.rnait-line{
    width: 868px;
    height: 2px;
    background-color: var(--color-light-blue);
    margin-bottom: 40px;
}
.rnait-content
{
    .korporativnoyeupravleniye-title.reglamentiruyushchiyedokumenty-title.uslugi-sections{
        margin-bottom: 40px;
    }
}
.rnait-card-description{
    display: grid;
    grid-template-columns: 1fr 1fr;
    @include mobile-768{
        grid-template-columns: 1fr;
    
    }
    row-gap: 40px;
    column-gap: 50px;
    margin-bottom: 40px;   
}
.rnait-card-item{
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    span:first-child{
        @include font-d-h6;
            color: var(--color-blue);
    }
    span:last-child{
        @include font-d-t2;

    }
}
.rnait-card-price{
    display: flex;
    gap: 15px;
    span{
        @include font-d-h5;
    }
}
.rnait-card-imgs{
    margin-top: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    @include notebook-1280{
        grid-template-columns: 1fr 1fr 1fr;
    }
    @include mobile-768{
        grid-template-columns: 1fr;
    }
    gap: 20px;
}
.rnait-card-contact.rnait-card-item{
    span{
        span{
            a{
                text-decoration: underline;
            }
        }
    }
}
.rnait-card--title{
    span{
        @include mobile-768{
            font-size: 32px;
        }
    }
}