.kontaktyneftebaz-content{
    
    @include mobile-768{
        margin-top: -20px;
        overflow-x: scroll !important;
    }


    table{
        @include mobile-768{
            table-layout: fixed;
        }
        width: 860px;
        tr{

            font-size: 20px;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: 0px;

        }
        tr:first-child{
            font-size: 22px;
            font-weight: 500;
            line-height: 26px;
            letter-spacing: 0px;
        }

    }
}
.kontaktyneftebaz--select{

    select{
        @include mobile-768{
            width: 335px !important;
        }
    }
}