.mapazs--list--dowload{
    margin-top: 40px !important;
    font-weight: 400;
    font-size: 20px;
    line-height: 180%;
    text-decoration: underline;
    color: #0078D2;
    display: grid !important;
    grid-template-columns: 48px auto;
    gap: 15px;


}
.mapazs--list--dowload-data{
    font-weight: 400;
    font-size: 20px !important;
    line-height: 180% !important;
    color: black !important;
}
.mapazc-title{
    margin-top: 100px;
    gap: 15px !important;
    span{
        @include mobile-768{
            font-weight: 400;
            font-size: 32px !important;
            line-height: 120%;
        }
    }
}
.mapazc-content{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    font-weight: 400;
    font-size: 22px;
    line-height: 150%;

}

.mapazc-content-yuridicheskiy--list{
    padding-top: 20px;
    list-style-type: decimal !important;
    font-weight: 400;
    font-size: 22px;
    line-height: 150% !important;
    padding-left: 24px;
    margin-bottom: 50px;

}
.mapazc-title-bottom{
    font-size: 40px !important;
    margin-bottom: 30px;
    @include mobile-768{
        font-weight: 400;
        font-size: 32px !important;
        line-height: 120%;
    }
}
.mapazc-content-yuridicheskiy{
    padding-top: 30px !important;
    span{
        font-weight: 500;
        font-size: 28px;
        line-height: 150%;
        @include mobile-768{
            font-size: 22px;
            line-height: 120%;
        }
    }
}
.mapazs--main-title{
    font-weight: 400;
    font-size: 48px;
    line-height: 120%;
    @include mobile-768{
        font-weight: 400;
        font-size: 32px !important;
        line-height: 120%;
    }
}

