.mission-content-body{
    display: flex;
    flex-direction: column;
    gap: 100px;
    @include mobile-768{
        gap: 80px;
    }
} 
.mission-content-body--card{
    display: flex;
    flex-direction: column;
    gap: 15px;
} 
.mission-content-body--card--title{
    @include font-d-h2{
        font-size: 40px;
    }
    @include mobile-768{
        font-weight: 400;
        font-size: 32px;
        line-height: 120%;
    }
}
.mission-content-body--card--desc{
    @include font-d-s1;
    @include mobile-768{
        font-weight: 400;
        font-size: 22px;
        line-height: 150%;
    }
}
.mission-content-body--card--icon{
    margin-top: 30px;
    display: flex;
    gap: 15px;
    align-items: center;
    li{
        display: flex;
        align-items: center;
        font-weight: 400;
        font-size: 20px;
        line-height: 180%;
        text-decoration-line: underline;
        color: #0078D2;
        font-family: 'Roboto' !important;
    }
}