.korporativnoyeupravleniye-content{
    .korporativnoyeupravleniye-title{
        @include mobile-768{
            span{
                font-size: 32px !important;
            }
        }
    }
}
.main-table{
    @include mobile-768{
        width: 768px;
        table-layout: fixed;
        
    }
    margin-bottom: 100px;
    tr:first-child{
        padding: 17px 40px 17px 20px;
    }
    tr{
        padding: 30px 40px 30px 20px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        background-color: var(--color-light-blue);
    }
    tr:nth-child(2){
        background-color: white;
        @include font-d-t2{
            line-height: 30px;
        }
    };
    tr:nth-child(1){
        @include font-d-h6{
            line-height: 26.4px;
        }
    };
    tr:nth-child(3){
        @include font-d-t2{
            line-height: 30px;
        }
    };
    
}
.korporativnoyeupravleniye-title{
    //@include font-d-h2;
    margin-bottom: 30px;
    span{
        @include font-d-h2;
        //margin-bottom: 30px;
    }
}
.korporativnoyeupravleniye-subtitle{
    span{
        @include font-d-h5;
        @include mobile-768{
            font-weight: 500;
            font-size: 22px;
            line-height: 120%;
        }
    }
    margin-bottom: 30px;
    
}
.another-table{
    margin-bottom: 150px;
}
.korporativnoyeupravleniye-botton-content{

    .otvetstvennost-card {
        .otvetstvennost-card-container{
            .otvetstvennost-card-text{
                @include mobile-768{
                    font-size: 24px;
                }
            }
        } 
            
    }

    display: flex;
    flex-direction: column;
    row-gap: 51px;
}
.korporativnoyeupravleniye-botton--select{
    display: flex;
    flex-direction: column;
    row-gap: 12px;
    select, select option{
        font-weight: 500;
        font-size: 22px;
        line-height: 16px;
        color: #000000;

    }


}
.korporativnoyeupravleniye-botton--label
{
    color: #0078D2;
    font-weight: 400;
    font-size: 18px;
    line-height: 12px;

}
.korporativnoyeupravleniye-botton--selector{
    outline: none;
    border: none;
    border-bottom: 2px solid var(--color-blue);
    width: 470px;

    padding-bottom: 17px;
    @include mobile-768{
        width: 335px;
    }


}
.select-content{
    display: flex;
    align-items: center;
    column-gap: 15px;
    span{
        @include font-d-t2;
            color: var(--color-blue);
            text-decoration: underline;

    }
}
.korporativnoyeupravleniye--table{
    
    @include mobile-768{

        overflow-x: scroll;
        .main-table{
            margin-bottom: 0px;
        }
        margin-bottom: 80px;
        
    }

}
.korporativnoyeupravleniye-botton--body{
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 15px;
    margin-top: 40px;
    a{
        flex: 1;

        span{
                font-weight: 400;
                font-size: 20px;
                line-height: 150%;
                align-items: center;
                text-decoration-line: underline;
                color: #0078D2;
        }
    }
    .pdf__icon{

    }
}
