.mapazs-content-map{
    position: relative;
    width: 100% !important;
    height: 560px;
    display: flex;
    flex-direction: row;
    @include mobile-768{
        flex-direction: column;
        height: auto;
    }
}
//скрываем логотиа Яндекс ежже
[class*= "copyrights-pane"] {
    display: none !important;
}
.mapazs-content-right--option{
    flex-basis: 310px;
    height: 100%;
    background: #EAF5FF;
    overflow-y: scroll;
    overflow-x: hidden;
    @include mobile-768{
        width: 100%;
        flex: 1;
    }
}
#map{
    flex-basis: 550px;
    height: 560px;
    @include notebook-1280{
        width: auto !important;
        flex: 1 !important;
    }
    @include mobile-768{
        flex-basis: 400px !important;
    }

}
.mapazs-content-right--option{
    padding: 17.65px 25px 0 20px;
    @include mobile-768{
        padding: 20px 40px;
    }
}
.mapazc--map--input{
    position: relative;
    margin-bottom: 26.22px;
    // overflow-y: scroll;

    min-width: 255px;
    height: 44.13px;
    input{
        width: 100%;
        height: 100%;
        font-size: 18px;
        font-weight: 400;
        line-height: 18px;
        background: transparent;
        outline: none;
        border: none;
        border-bottom: 2px solid #0078D2;


    }
    @include mobile-768{
        width: 100%;
    }
}
.searchmap--icon{
    cursor: pointer;
    position: absolute;
    bottom: 10.88px !important;
    right: 1.95px;
    width: 24px;
    height: 24px;
}
.mapazc--map--options{
    display: flex;
    column-gap: 5px;
    margin-bottom: 20px;
    span{
        font-size: 22px;
        font-weight: 500;
        line-height: 33px;
        letter-spacing: 0px;
        @include mobile-768{
            font-size: 20px;
        }


    }
}
.chevron-down-map--icon{
    width: 24px;
    height: 24px;

}
.mapazc--map--options--icon{
    display: grid;
    place-items: center;

}
.map__rejim__raboty--body--btn{
    display: flex;
    justify-content: center;

    button{
        width: 220px;
        height: 50px;
        border: 2px solid #0078D2;
        background: transparent;
        border-radius: 5px;
        display: grid;
        place-items: center;
        span{
            color: #0078D2;
            font-size: 20px;
            font-weight: 500;
            line-height: 20px;
    
    
    
        }
        &:hover{
            background-color:#0078D2;
            border-radius: 5px;
            transition: .5s;
            span{
                color: #EAF5FF !important; 
            }
        }
    }


}


.map__osobennost, .azc--box{

    ul{
        padding-bottom: 20px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 10px;
        li{
            &:hover{

            }
        }
    }
}
.map__rejim__raboty--body--icon{
    width: 40px;
    height: 40px;

    cursor: pointer;
}
.map__toplivo {
    ul{
        padding-bottom: 20px;
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
        li{
            padding: 6px 12px;
            background: #B8B8B8;
            font-family: Roboto;
            font-size: 16px;
            font-style: italic;
            font-weight: 600;
            line-height: 16px;
            color: white;


        }
    }
}
.mapazc--map--options, .mapazc--map--options--open{
    span{
            cursor: pointer;
    }

}
.map__osobennost--body,  .map__toplivo--body,  .map__rejim__raboty--body {
    display: none;
    flex-direction: column;
}
.ballon--content{
    width: 593px;
    height: 419px;
}
.map__rejim__raboty--main--content{
    margin-top: 22px;
    padding-top: 18px;
    display: flex;
    flex-direction: column;
    row-gap: 30px;
}
.azc--title{
    display: flex;
    column-gap: 5px;
    span{
        font-size: 24px;
        font-weight: 600;
        line-height: 28px;
        letter-spacing: 0em;
        color: #0078D2;
        @include mobile-768{
            font-size: 22px;
        }
    }
}
.azc--location{
    margin-top: 10px;
    span{
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;
        @include mobile-768{
            font-size: 16px;
        }
    }
}
.azc--box{
    display: none;
    ul{
        padding: 0 !important;
        padding-top: 10px !important;
        li{
            .map__rejim__raboty--body--icon path, .map__rejim__raboty--body--icon polygon, .map__rejim__raboty--body--icon{
                fill: #0078D2 !important;
            }
        }
    }
}
.azc6--box {
    padding-bottom: 30px;
}