@font-face {
    font-weight: 400;
    font-family: Roboto Regular;
    src: url("../fonts/Roboto/Roboto-Regular.ttf");
}

@import "import/reset";
@import "import/fonts";
@import "adaptivenamana";
@import './font-mixin.scss';



*{
    box-sizing: border-box;
    outline: hidden;
    margin: 0;
    padding: 0;
    font-style: normal;
    word-wrap:break-word;
    ::-webkit-scrollbar {
        border-radius: 5px;
        height: 2px;
        background: #EAF5FF;
    }
    ::-webkit-scrollbar-thumb {
        background-color: #0078D2; /* green */
        border-radius: 5px;
        height: 2px;
    
    }

    //мб удалю ежже


}
.paddingtop80px{
    padding-top: 80px;
}
.text-text{
    text-align: justify !important;
    word-break: break-all;
    -webkit-hyphens: auto;
    -moz-hyphens: auto;
    -ms-hyphens: auto;
    hyphens:manual;
}

html, body{
    scrollbar-color: #0078D2 #EAF5FF   !important; /* «цвет ползунка» «цвет полосы скроллбара» */
    scrollbar-width: auto;
    scrollbar-width: thin;
 
}
:root
{
    --color-blue: #0078D2;
    --color-light-blue: #EAF5FF;
    --color-black: #000000;
    --color-white: #FFFFFF;
    --color-gray: #B8B8B8;
    --color-light-gray: #D9D9D9;
    --color-gradient: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.55) 100%);
    --color-blue-gradient: linear-gradient(90deg, #0082D3 0%, #0072CC 100%);
    --color-linear-gradient: linear-gradient(226.09deg, #1EA5F9 10.23%, #0072CC 87.13%);

}



.antimonopolnyykomplayens-target--list--dowload{
    margin-top: 30px;
}

.icon{
    width: 24px;
    height: 24px;
}

body{
    width: 100vw;
    max-width: 100%;
    font-family: "Roboto", sans-serif;
    background-color: #FFFFFF;
    margin: 0 auto;
    //word-wrap: break-word;
    hyphens: auto;
    //height: 100vw;

}
.container{
    max-width: 1440px;
    margin: 0 auto;

}
section{
    margin: 0 70px 120px 70px;
    @include notebook-1280{
        margin: 0 20px 80px 20px;
    }
    @include mobile-768{
        margin: 0 20px 80px 20px;
    }
}
.section-title{
    @include font-d-h2;
    margin-bottom: 40px;
}
.img-icon 
{
    display: grid;
    place-items: center;
}
.left-menu-main-link{
    @include font-d-t2;
} 	


.another-table{
    tr{
        background-color: var(--color-light-blue);
    }
    tr:nth-child(2n){
        background: white;
    };
}
.themain--select{
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    row-gap: 12px;
    select, select option{
        font-weight: 500;
        font-size: 22px;
        line-height: 16px;
        color: #000000;
    }
}
.themain-select--label
{
    color: #0078D2;
    font-weight: 400;
    font-size: 18px;
    line-height: 12px;
}
.themain--select--selector{
    outline: none;
    border: none;
    border-bottom: 2px solid var(--color-blue);
    width: 470px;
    padding-bottom: 17px;
}


.btn-main-btn{
    display: grid;
    place-items: center;
    background: #0078D2;
    border-radius: 5px;
    padding: 5px 15px;
    width: 200px;
    height: 60px;
    a{
        font-weight: 500;
        font-size: 20px;
        line-height: 100%;
        color: white;
    }
}
.drop-content{
    //width: 750px;
    display: flex;
    flex-direction: column;
    row-gap: 50px;
}

.drop-card-content{
    .value {
        font-family: Roboto;
        font-size: 22px;
        font-weight: 500;
        line-height: 33px;
        letter-spacing: 0px;
        text-align: left;
        color: #0078D2;
    }
    span{
        span{
            font-family: Roboto;
            font-size: 22px;
            font-weight: 400;
            line-height: 33px;
            letter-spacing: 0px;
            text-align: left;
            color: black;
        }
    }
}
.drop-card-content{
    display: flex;
    flex-direction: column;
    row-gap: 33px;
    margin-top: 31px;
}
.drop-card-title-bottomtitle
{
    @include font-d-h5;
}
.drop-card-title-bottomtitle-list  {
    margin-top: 20px;
}
.drop-card-container 
{
    display: inline-flex;
    column-gap: 10px;
}
.drop-card-text{
    text-align: justify !important;
    word-break: break-all;
    -webkit-hyphens: auto;
    -moz-hyphens: auto;
    -ms-hyphens: auto;
    hyphens:manual;
    @include font-d-h4;
    @include mobile-768{
        font-weight: 400;
        font-size: 24px !important;
        line-height: 120%;

    }
}
img{

    border-radius: 5px;
    display: block;
    margin: auto;
    transition: 0.3s;
    width: 100%;
    height: 100%;
    object-fit: cover;	

}

input{
    // z-index: -2;
    position: relative;
    overflow: visible;
}


.drop-card-icon{
    display: grid;
    place-items: center;
}
.chevron-up-icon{
    width: 40px;
    height: 40px;
    transform: rotate(180deg);
    transition: 0.3s;
}
.drop-card-content{
    display: none;
}
.displayblock{
    display: flex;
}
.rotatedefault{
    transform: rotate(0);
    transition: 0.3s;
}

.icon-helper{
    display: flex;
    align-items: flex-start;

}


