//select

.select-select{
    display: flex;
    flex-direction: column;
    row-gap: 12px;
    margin-bottom: 40px;

    select, select option{
        font-weight: 500;
        font-size: 22px;
        line-height: 16px;
        color: #000000;

    }


}
.select-label
{
    color: #0078D2;
    font-weight: 400;
    font-size: 18px;
    line-height: 12px;

}
.select-selector{
    outline: none;
    border: none;
    border-bottom: 2px solid var(--color-blue);
    width: 470px;
    padding-bottom: 17px;
    background-color: transparent;


}
//pagination



.zakupochnyyeprotsedury-pagination--left-selector, .asdasd{
    outline: none;
    background-color: transparent;
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    position: relative;
    outline: none;
    padding: 5px;
    padding-right: 40px;

}
.zakupochnyyeprotsedury-pagination--left{
    display: flex;

}
.zakupochnyyeprotsedury-pagination--left-label{
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    select{
        color: var(--color-blue);
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
        margin: 0;
        padding: 0;
        padding-left: 5px;
        width:fit-content;

    }
}
.chevron-down__select-icon{
    width: 18px;
    height: 18px;
    display: grid;
    place-items: center;
}
.zakupochnyyeprotsedury-pagination--right, .zakupochnyyeprotsedury-pagination{
    display: flex;
    justify-content: space-between;
    column-gap: 30px;
    @include mobile-768{
        column-gap: normal;
    }

    a{
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
        text-decoration: underline;
    }
}
.zakupochnyyeprotsedury-pagination--right--links{
    ul{
        display: flex;
        flex-direction: row;
        column-gap: 20px;
        @include mobile-768{
            gap: 0;
            justify-content: space-between;
        }

        li{
            display: grid;
            place-items: center;
            a{
                font-weight: 500;
                font-size: 18px;
                line-height: 120%;
                text-decoration: underline;
            }
        }
    }
}

