}

// Пагинация
.zakupochnyyeprotsedury-pagination
{
    position: relative;
    .zakupochnyyeprotsedury-pagination--left{
        @include mobile-768{
            position: absolute;
            overflow: visible;
            left: 0;
            bottom: -64px;

        }
    }
}
.zakupochnyyeprotsedury-pagination--right{
    @include mobile-768{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        
    }
}
.zakupochnyyeprotsedury-pagination--right--links{
    ul{
        @include mobile-768{
    
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                gap: 20px;
      

        }
    }

}
.zakupochnyyeprotsedury-pagination{
    @include mobile-768{
        display: flex;
        flex-direction: column;
        justify-content: space-between;

    }
}
