
.minivector__icon{
    width: 8px;
    height: 14px;
}
.novosti-card-right-link--icon
{
    display: grid;
    place-items: center;
}
.novosti-card{
   
    //height: 192px;
    display: grid;
    grid-template-columns: 310px 1fr;
    column-gap: 20px;
    @include mobile-768{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }
}
.novosti-card-right--data--img{
    width: 310px;
    height: 192px;
    background-color: #C4C4C4;
    z-index: 10;
    border-radius: 5px !important;
    @include mobile-768{
        width: 335px;
        height: 207px;

    }


}

.novosti-card-left{
    display: block;
    position: relative;
   
}
.novosti-card-right{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.novosti-card-right--link{
    display: flex;
    column-gap: 10px;
    @include font-d-s2;
}
.novosti-card-right--data{
    margin-bottom: 20px;
    @include font-d-s3{
        color: #0078D2;
    }
}
.novosti-card-right--title{
    margin-bottom: 30px;
    @include font-d-h6;
    @include mobile-768{
        font-size: 20px;
    }
}
.novosti-content{
    display: flex;
    flex-direction: column;
    gap: 40px;
}
.novosti-card-right-link--text{
    font-style: italic !important;
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
}