.optovayarealizatsiya-content{
    margin-bottom: 100px;
    @include mobile-768{
        margin-bottom: 80px;
    }
}
.optovayarealizatsiya-primery-docs--buttom--downloads{
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 15px;
    @include mobile-768{
        display: grid;
        grid-template-columns: 48px 1fr;
    }
    a{
        @include font-d-link;
            line-height: 180%;
            
    }
}
.optovayarealizatsiya-primery-docs--buttom-text  
{

    margin-top: 50px;
    span{
        @include font-d-t1;
    }
}
.optovayarealizatsiya-description{
    span{
        @include font-d-t1;
    }
    margin-bottom: 50px;
}
.optovayarealizatsiya-primery-docs{
    span{

        @include font-d-h5;
        @include mobile-768{
            font-size: 22px;
        }
        
    }
    ul{
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        row-gap: 20px;
        li{
            @include font-d-t1;
        }
    }

    
}
.optovayarealizatsiya-content--bottom{
    .korporativnoyeupravleniye-title.reglamentiruyushchiyedokumenty-title{

        margin-top: 100px;
        @include mobile-768{
            margin-top: 80px !important;
        }
        .uchreditelnyyedokumenty-content.antimonopolnyykomplayens-content{
            margin-top: 30px;
        }
    }

}
.optovayarealizatsiya-content--midle{
    ul{
        display: flex;
        flex-direction: column;
        gap: 20px;
        li{
            @include font-d-t1;
        }
    }
}
.optovayarealizatsiya-content--bottom{
    .reglamentiruyushchiyedokumenty-title{
        .antimonopolnyykomplayens-content{
            row-gap: 40px;
            .antimonopolnyykomplayens-target--list--dowload{
                a{
                    @include mobile-768{

                        text-decoration: underline;;
                        text-underline-position: under;


                    }
                }
            }
        }
    }
}