.the-main-content{
    display: grid;

    grid-template-columns: minmax(auto, 440px) 1fr;
    margin: 0 auto;
    @include notebook-1280{
        display: flex;
        flex-direction: column;

    }
    @include mobile-768{
        
    }

}
.left-menu-content{

    margin-top: 55px;
    height: auto;
    width: 80%;

    @include notebook-1280{
        margin-top: 0;
        width: 100%;
    }

}
.left-menu-main-links{
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin: 0;
    & > *:last-child{
        margin-bottom: -30px;
    }

   
}
.right-content-main{
    margin: 0;
    margin-top: 34px;
    @include notebook-1280{
        margin-top: 50px;
    }
    
}
.left-menu-main {
    position: sticky;
    //если хедер фиксированынй
    top: 110px;
    //если хедер не фиксированный
    // top: 30px;

    @include notebook-1280{
        margin-top: 50px;
    }
    @include mobile-768{
        margin-top: 40px;
    }


}
.left-menu-main-link{
    margin: 0;
}
.left-menu-main-links--left, .left-menu-main-links--right{
    display: flex;
    row-gap: 30px;
    flex-direction: column;
}  
.left-menu-main-links{
    @include notebook-1280{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .left-menu-main-links--left{
            flex: 1;

        }
        .left-menu-main-links--right{
            flex: 1;
        }
    }
    @include mobile-768{
    
        flex-direction: column !important;
    }
    
}
.right-content-main--title{
    @include mobile-768{
        font-weight: 400;
        font-size: 40px;
        line-height: 150%;
        margin-bottom: 50px;
    }
}
.right-content-main--subtitle{
    span{
        @include mobile-768{
            font-weight: 400;
            font-size: 28px;
            line-height: 150%;
        }
    }
}
.right-content-main--body--text{
    @include mobile-768{

    }

}

.left-menu-main-links--right{
    @include mobile-768{
        //новое от 22ю05ю23
        padding-bottom: 25px !important;
    }
}
.top110{
    top: 110px;
}