.rukovodstvo-content{
    // @include table{
    //     max-width: 728px !important;
    // }
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    row-gap: 50px;
    margin: 0 auto;
    @include mobile-768{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 0;
        row-gap: 20px;
    }
    @include notebook-1280{
        grid-template-columns: 354px 354px;
        justify-content: center;
    }
}
.rukovodstvo-card{
    width: 420px;
    // @include table{
    //     max-width: 354px;
    // }
    min-height: 429px;
    display: flex;
    flex-direction: column;
    @include notebook-1280{
        width: 354px;
        height: 429px;
    }
    @include mobile-768{
        width: 335px;
        height: 415px;
    }
}
.rukovodstvo-card--img {
    width: 420px;
    height: 260px;
    border-radius: 5px;
    background-color: #D9D9D9;
    margin-bottom: 25px;
    @include notebook-1280{
        width: 354px;
    }
    @include mobile-768{
        width: 335px;
        height: 248px;
    }
    // @include table{
    //     width: 354px;
    //     height: 260px;
    // }
}
.rukovodstvo-card--description{
    display: flex;
    flex-direction: column;
    row-gap: 8px;
}
.rukovodstvo-card--name{
    @include font-d-h4;
    max-width: 400px;
    margin-bottom: 8px;
}
.rukovodstvo-card--desc{
