
.right-content-main--title{
    @include font-d-h1{
        color: #0078D2;
    }
    margin-bottom: 50px;
}
.right-content-main--subtitle 
{
    padding-left: 25px;
    border-left: 4px solid var(--color-blue);
    span{
        @include font-d-h3;
    }
    margin-bottom: 30px;
}
.right-content-main--body--text{
    display: flex;
    span{
        flex: 1;
    }
    @include font-d-t1;
    margin-bottom: 50px;
    @include mobile-768{
        font-weight: 400;
        font-size: 20px;
        line-height: 150%;
    }
}
.right-content-main--content{
    margin-bottom: 100px;
    @include notebook-1280{
        margin-bottom: 50px;
    }
    // display: flex;
    // flex-direction: column;
    //justify-content: center;
    //align-items: center;

}
.right-content-main--content--top {
    display: flex;
    justify-content: center;

    .right-content-main--content--top--image{
        border-radius: 5px;
        flex: 1;
        max-width: 860px;
        height: 540px;
        background-color: #D9D9D9;
        margin-bottom: 50px;
        @include notebook-1280{
            max-width: 727px;
            height: 540px;
        }
        @include mobile-768{
            max-width: 335px;
            height: 250px;
        }
    }


    // @include table{
    //     max-width: 727px;
    //     height: 540px;
    // }
}
.right-content-main--content--bottom{
    display: flex;
    flex-direction: row;
    //align-items: center;
    //gap: 73px;
    justify-content: space-between;
    @include notebook-1280{
        gap: 38px;
    }
    @include mobile-768{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}
.right-content-main--content--bottom--card{
    max-width: 180px;
    height: 205px;
    flex: 1;
    @include mobile-768{
        display: flex;
        flex-direction: column;
        justify-self: center;

    }
} 
.right-content-main--content--bottom--card-title{
    font-weight: 400;
    font-size: 100px;
    line-height: 100%;
    color: #0078D2;
    margin-bottom: 5px;
    @include mobile-768{
        font-weight: 400;
        font-size: 60px;
        line-height: 100%;
    }
}
.right-content-main--content--bottom--card-subtitle{
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: #0078D2;
    margin-bottom: 10px;
    @include mobile-768{
        font-weight: 400;
        font-size: 20px;
        line-height: 150%;
    }
}
.right-content-main--content--bottom--card-description{
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    @include mobile-768{
        font-weight: 400;
        font-size: 20px;
        line-height: 150%;
    }
}
.right-content-main-bottom {
    display: flex;
    flex-direction: column;
    gap: 20px;
    h4{
        font-weight: 500;
        font-size: 28px;
        line-height: 150%;
        //margin-bottom: 20px;
        @include mobile-768{
            font-weight: 500;
            font-size: 22px;
            line-height: 150%;
        }
    }
    li{
        font-weight: 400;
        font-size: 22px;
        line-height: 150%;
        @include mobile-768{
            font-weight: 400;
            font-size: 20px;
            line-height: 150%;
        }
    }
    // @include table{
    //     margin-bottom: 80px;
    // }
}
.right-content-main{
    margin-bottom: 100px;
}